import Vue from 'vue'
import VueRouter from 'vue-router'
import CalculatorView from '../views/CalculatorView.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Calculator',
    component: CalculatorView
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
